
import { defineComponent, onMounted } from 'vue'
import { TimelineLite, Power1, Back, Elastic, Expo } from 'gsap'

export default defineComponent({
  name: 'OpeningSlide',
  setup () {
    const tl = new TimelineLite({
      defaults: {
        ease: Power1.easeOut
      }
    })
    onMounted(() => {
      const intro = '#opening h1 span'
      const home = '#opening .button-wrap button'
      const plus = '#opening .button-wrap .plus'
      tl.to(intro, {
        y: '0%',
        duration: 1,
        stagger: 0.25
      })
      tl.to(home, {
        y: '0%',
        opacity: 1,
        duration: 1
      }, '-=0.5')
      tl.to(home, 0.4, {
        scale: 0.8,
        rotation: 16,
        ease: Back.easeOut.config(1.7)
      })
      tl.to(plus, 0.8, {
        scale: 0.9,
        opacity: 1
      }, '-=0.6')
      tl.to(home, 1.2, {
        scale: 1,
        rotation: '-=16',
        ease: Elastic.easeOut.config(2.5, 0.5)
      })
      tl.to(plus, 1.1, {
        scale: 3,
        opacity: 0,
        ease: Expo.easeOut
      }, '-=1.2')
    })
    const handleOnSlide = () => {
      tl.to('#opening', {
        y: '-100%',
        duration: 1
      })
    }
    return {
      handleOnSlide
    }
  }
})
